* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    background-color: #263238;
    color: white;
  }
  
  h1,
  h2,
  h3,
  h4,
  h5 {
    font-weight: normal;
  }
  
  header {
    padding: 10px;
    display: flex;
    align-items: end;
    justify-content: space-between;
  }
  
  .btn-danger {
    border: 1px solid transparent;
    border-radius: 4px;
    padding: 6px 14px;
    background-color: #f44336;
    color: white;
    font-family: inherit;
    font-size: 14px;
  }
  
  .hide {
    display: none !important;
  }
  
  form {
    max-width: 450px;
    margin: 30px auto;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4);
    border-radius: 8px;
    padding: 20px;
  }
  
  input {
    display: block;
    width: 100%;
    border-radius: 8px;
    border: 2px solid transparent;
    height: 34px;
    padding: 5px;
    background: #37474f;
    color: inherit;
    font-family: inherit;
  }
  
  input::placeholder {
    color: #aaa;
  }
  
  .input-container {
    margin-bottom: 20px;
    display: flex;
    align-items: baseline;
    gap: 5px;
  }
  
  .btn-primary {
    border: 1px solid transparent;
    border-radius: 4px;
    padding: 6px 14px;
    background-color: #1565c0;
    color: white;
    font-family: inherit;
    font-size: 14px;
  }
  
  form h2,
  .conference-section h2 {
    margin-bottom: 20px;
  }
  
  .conference-section {
    padding: 20px 30px;
    max-width: 960px;
    margin: 0 auto;
  }
  
  .conference-section h2 {
    text-align: center;
    font-size: 32px;
    padding-bottom: 10px;
    border-bottom: 1px solid #546e7a;
  }
  
  #peers-container {
    display: grid;
    grid-template-columns: repeat(3, minmax(min-content, 1fr));
    place-items: center;
    grid-gap: 10px;
  }
  
  .peer-video {
    height: 250px;
    width: 250px;
    border-radius: 40%;
    object-fit: cover;
    margin-bottom: 10px;
  }
.peer-video-tile {
    width: 250px;
    border-radius: 40%;
    object-fit: cover;
    margin-bottom: 10px;
}
  .local.peer-video {
    transform: scaleX(-1);
  }
  
  .peer-name {
    font-size: 14px;
    text-align: center;
  }
  
  .peer-tile {
    padding: 10px;
  }
  
  .control-bar {
    display: flex;
    position: fixed;
    bottom: 0;
    width: 100%;
    padding: 15px;
    justify-content: center;
    z-index: 10;
  }
  
  .control-bar > *:not(:first-child) {
    margin-left: 8px;
  }
  
  .btn-control {
    font-size: 12px;
    text-transform: uppercase;
    letter-spacing: 1px;
    border: 2px solid #37474f;
    width: 64px;
    height: 64px;
    border-radius: 50%;
    text-align: center;
    background-color: #607d8b;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
    color: white;
  }

  .separator {
    font-size: 12px;
  }

.conference {
    display: grid;
    grid-template-columns: 1fr 300px;
    gap: 1em;
}

.presenter-video {
    height: 250px;
    width: 250px;
    border-radius: 20%;
    object-fit: cover;
    margin-bottom: 10px;
}

.screen-share-video {
    width: 100%;
    height: 80vh;
    object-fit: cover;
}
.video-section-border {
    border: 1px solid !important;
}
.connection-network {
    width: 20px !important;
    height: 28px !important;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: black;
    border-radius: 50%;
}
.shape-circle {
    border-radius:40%;
}
.shape-color {
    background-color: rgb(233, 30, 99);
}
.tile-short {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-family: inter,sans-serif;
    aspect-ratio: 1.2/0.5;
    font-weight: 600;
    font-size: 1.25em;
}

/*TSX Class start*/
.textEllipsis-75 {
    max-width: 75%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    min-width: 0;
}
.flexCenter {
    display: 'flex'; justify-content: center; align-items: center;
}
.avatar-container {
    flex-direction: column;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    width: 40%;
    height: 100%
}

.avatar-common 
{
    color: white;
    font-family: sans-serif;
    aspect-ratio: 1;
    font-weight: 600;
    font-size: 9px;
}
.avatar-common-circle {
    border-radius:50%;
}
.text-var-body2 {
    font-size: 0.875rem;
    line-height: 1.25rem;
    /*font-weight: $medium;*/
    letter-spacing: 0.25px;
    height: 18px !important;
    font-size: 10px;
}


/*TSX Class end*/


